 /**
  * @description: Numeral 
  * @Author: tanhaolong
  * @Date: 2021-12-13 15:34:08
  * @param {Number} num 
  * @param {Int} type CHINESENUMERAL COMMASEPARATEDUNCOMMASEPARATED
  * @return {Array} 
  */
export default function Numeral ({num , type}){
        if(type.toUpperCase() ==  'CHINESENUMERAL'){
            //        
            if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(num)){
                //0
                return "";  
            }
            var unit = "", str = "";
            num += "00";  
            var indexpoint = num.indexOf('.');  // 
            if (indexpoint >= 0){
                num = num.substring(0, indexpoint) + num.substr(indexpoint+1, 2);   // unit
            }
            unit = unit.substr(unit.length - num.length);  // unit
            for (var i=0; i < num.length; i++){
                str += "".charAt(num.charAt(i)) + unit.charAt(i);  //
            }
            return str.replace(/(|||)/g, "").replace(/()+/g, "").replace(/(||)/g, "$1").replace(/()|()/g, "$1$2").replace(/^?|/g, "").replace(/$/g, ""); // 
        }else if(type.toUpperCase() == 'COMMASEPARATED'){
            // 
            if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(num)){
                //0
                return "";  
            }
            num=num.toString().split(".");  // 
            var arr=num[0].split("").reverse();  // 
            var res=[];
            for(var i=0,len=arr.length;i<len;i++){
              if(i%3===0&&i!==0){
                 res.push(",");   // 
              }
              res.push(arr[i]);
            }
            res.reverse(); // 
            if(num[1]){  // 
              res=res.join("").concat("."+num[1]);
            }else{
              res=res.join("");
            }
            return res;
        }else if(type.toUpperCase() == 'UNCOMMASEPARATED'){
            //
            let str =num
            str = str.replace(/,/g, "");// 
            return Number(str);
        }else{
           return "type";
        } 

}